import components from '@lekoarts/gatsby-theme-minimal-blog/src/gatsby-plugin-theme-ui/components'
import React from 'react'
import Gravatar from 'react-gravatar'

export const Author = ({ email, name, description }) => {
  return (<div style={{ display: "flex", alignItems: "center" }}>
    <Gravatar email={email} style={{ borderRadius: "25px", marginRight: 8 }} />
    <div>
      <p style={{ marginBottom: "-4px" }}><b>{name}</b></p>
      <p style={{ marginTop: "-4px" }}>{description}</p>
    </div>
  </div>)
}

export default { ...components, Author: Author }
